<template>
  <div class="text-center signIn-box">
    <section class="form-signin w-100 m-auto">
      <h1 class="h3 mb-3 fw-normal">Î°úÍ∑∏Ïù∏</h1>
      <!-- ÏïÑÏù¥Îîî -->
      <div class="form-floating">
        <input
          type="text"
          id="floatingInput"
          class="form-control"
          v-model="signInID"
          v-on:keyup.enter="keyEnter()"
          @input="checkID"
        />
        <label for="floatingInput">ÏïÑÏù¥Îîî</label>
      </div>
      <div v-if="!isIDValid && signUP" class="text-danger small">
        ÏïÑÏù¥ÎîîÎäî ÏòÅÎ¨∏Í≥º Ïà´ÏûêÎ°ú ÏÑ§Ï†ïÌï† Ïàò ÏûàÏúºÎ©∞ 5ÏûêÏù¥ÏÉÅÏúºÎ°ú ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.
      </div>
      <!-- Ïù¥Î¶Ñ : ÌöåÏõêÍ∞ÄÏûÖ ÏãúÏóêÎßå ÌëúÏãú -->
      <div class="form-floating">
        <input
          v-show="signUP"
          type="text"
          class="form-control"
          id="floatingName"
          v-model="signUpName"
          v-on:keyup.enter="keyEnter()"
        />
        <label for="floatingName">Ïù¥Î¶Ñ</label>
      </div>
      <!-- ÎπÑÎ∞ÄÎ≤àÌò∏ -->
      <div class="form-floating">
        <input
          type="password"
          class="form-control"
          id="floatingPassword"
          v-model="signInPW"
          v-on:keyup.enter="keyEnter()"
          @input="checkPW"
        />
        <label for="floatingPassword">ÎπÑÎ∞ÄÎ≤àÌò∏</label>
      </div>
      <div v-if="!isPWValid && signUP" class="text-danger small">
        ÎπÑÎ∞ÄÎ≤àÌò∏Îäî ÏòÅÎ¨∏, Ïà´Ïûê, ÌäπÏàòÎ¨∏ÏûêÎ•º Ìè¨Ìï®Ìï¥ 8Ïûê Ïù¥ÏÉÅÏúºÎ°ú ÏûëÏÑ±Ìï¥Ï£ºÏÑ∏Ïöî.
      </div>
      <button
        v-show="!signUP"
        class="w-100 mb-2 btn btn-lg btn-signIn"
        @click="signIn()"
        :disabled="!isIDValid || !isPWValid"
      >
        Î°úÍ∑∏Ïù∏
      </button>
      <label
        v-show="signUP"
        class="w-100 mb-2 btn btn-lg btn-signUp"
        @click="signUp()"
        :disabled="!isIDValid || !isPWValid"
        >ÌöåÏõêÍ∞ÄÏûÖ</label
      >
      <span
        v-show="!signUP"
        class="h6 fw-light text-decoration-underline"
        role="button"
        @click="signUP = true"
        >ÌöåÏõêÍ∞ÄÏûÖ</span
      >
    </section>
  </div>
</template>

<script>
import { mapState, mapActions } from "pinia";
import { useDiaryStore } from "../stores/store.js";

export default {
  name: "signIn-view",
  data() {
    return {
      signInID: "",
      signInPW: "",
      signUpName: "",
      signUP: false,
      isIDValid: true,
      isPWValid: true,
    };
  },
  computed: {
    ...mapState(useDiaryStore, ["isLoggedIn"]),
  },
  methods: {
    ...mapActions(useDiaryStore, ["signInDiary", "signUpDiary"]),
    keyEnter() {
      this.signUP === true ? this.signUp() : this.signIn();
    },
    //ÏïÑÏù¥Îîî ÌôïÏù∏(5Ïûê Ïù¥ÏÉÅ, ÏòÅÎ¨∏)
    checkID() {
      this.isIDValid = /^[a-zA-Z\d]{5,}$/.test(this.signInID);
    },
    //ÎπÑÎ∞ÄÎ≤àÌò∏ ÌôïÏù∏(ÏòÅÎ¨∏/Ïà´Ïûê/ÌäπÏàòÎ¨∏Ïûê Ìè¨Ìï® 8Ïûê Ïù¥ÏÉÅ)
    checkPW() {
      this.isPWValid =
        /^(?=.*[a-zA-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,}$/.test(
          this.signInPW
        );
    },
    // Î°úÍ∑∏Ïù∏
    async signIn() {
      try {
        if (!this.signInID) {
          alert("IDÎ•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî.");
          document.getElementById("floatingInput").focus();
          return;
        }
        if (!this.signInPW) {
          alert("passwordÎ•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî.");
          document.getElementById("floatingPassword").focus();
          return;
        }

        let data = {
          userid: this.signInID,
          pw: this.signInPW,
        };

        let result = await this.signInDiary(data);

        this.signInID = "";
        this.signInPW = "";

        if (result.success) {
          // Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µ
          alert(result.username + "Îãò ÏïàÎÖïÌïòÏÑ∏Ïöî.");
          // ÏÑ∏ÏÖòÏä§ÌÜ†Î¶¨ÏßÄÏóê Î°úÍ∑∏Ïù∏ Ï†ïÎ≥¥ Ï†ÄÏû•
          let userData = {
            name: result.username,
            id: result.user_id,
          };
          let jsonUserData = JSON.stringify(userData);
          sessionStorage.setItem("diary_user", jsonUserData);
          // Î°úÍ∑∏Ïù∏ ÏÉÅÌÉúÎ°ú ÏÑ§Ï†ï
          this.isLoggedIn = true;
          // Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µ Ïãú Î¶¨Ïä§Ìä∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
          this.$router.push({
            name: "calendar-view",
          });
          // location.href = "/";
        } else {
          // passwordMismatch, userInformationNotFound Ïã§Ìå®
          alert("Î°úÍ∑∏Ïù∏Ïóê Ïã§Ìå®ÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥ Ï£ºÏÑ∏Ïöî.");
        }
      } catch (error) {
        console.error("Error during sign in:", error);
        alert("Î°úÍ∑∏Ïù∏ ÏöîÏ≤≠ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥ Ï£ºÏÑ∏Ïöî.");
      }
    },
    // ÌöåÏõêÍ∞ÄÏûÖ
    async signUp() {
      if (!this.signInID) {
        alert("IDÎ•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî.");
        document.getElementById("floatingInput").focus();
        return;
      }
      if (!this.signUpName) {
        alert("Ïù¥Î¶ÑÏùÑ ÏûÖÎ†•ÌïòÏÑ∏Ïöî.");
        document.getElementById("floatingName").focus();
        return;
      }
      if (!this.signInPW) {
        alert("passwordÎ•º ÏûÖÎ†•ÌïòÏÑ∏Ïöî.");
        document.getElementById("floatingPassword").focus();
        return;
      }

      try {
        let result = await this.signUpDiary({
          userid: this.signInID,
          name: this.signUpName,
          pw: this.signInPW,
        });

        this.signInID = "";
        this.signInPW = "";
        this.signUpName = "";

        if (result) {
          if (result.success) {
            // ÌöåÏõêÍ∞ÄÏûÖ ÏÑ±Í≥µ
            alert("ÌöåÏõêÍ∞ÄÏûÖ ÎêòÏóàÏäµÎãàÎã§.");
            this.signUP = false; //login ÌôîÎ©¥ Î≥¥Ïó¨Ï£ºÍ∏∞
          } else {
            // ÌöåÏõêÍ∞ÄÏûÖ Ïã§Ìå®
            if (result.message === "alreadyExistsId") {
              // Ï§ëÎ≥µÏïÑÏù¥Îîî
              alert("Ïù¥ÎØ∏ Ï°¥Ïû¨ÌïòÎäî ÏïÑÏù¥ÎîîÏûÖÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥ Ï£ºÏÑ∏Ïöî.");
              console.log("already Exists Id :" + result.message);
            } else {
              alert("ÌöåÏõêÍ∞ÄÏûÖÏóê Ïã§Ìå®ÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥ Ï£ºÏÑ∏Ïöî.");
            }
          }
        }
      } catch (error) {
        if (
          typeof error === "object" &&
          error.error === "databaseConnectionFailed"
        ) {
          // Îç∞Ïù¥ÌÑ∞Î≤†Ïù¥Ïä§ Ïó∞Í≤∞ Ïã§Ìå®
          console.error("Îç∞Ïù¥ÌÑ∞Î≤†Ïù¥Ïä§ Ïó∞Í≤∞ Ïã§Ìå® : ", error);
          alert("ÌöåÏõêÍ∞ÄÏûÖ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥ Ï£ºÏÑ∏Ïöî.");
        } else {
          // Í∑∏ Ïô∏ Ïò§Î•ò Î∞úÏÉù
          console.error("ÌöåÏõêÍ∞ÄÏûÖ Ï§ë Ïò§Î•ò Î∞úÏÉù : ", error);
          alert("ÌöåÏõêÍ∞ÄÏûÖ ÏöîÏ≤≠ Ï§ë Ïò§Î•òÍ∞Ä Î∞úÏÉùÌñàÏäµÎãàÎã§. Îã§Ïãú ÏãúÎèÑÌï¥ Ï£ºÏÑ∏Ïöî.");
        }
      }
    },
  },
  watch: {
    signInID: "checkID",
    signInPW: "checkPW",
  },
};
</script>
  
  <style>
.signIn-box {
  height: 100%;
  display: flex;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 40px;
}

.form-signin {
  max-width: 330px;
  padding: 15px;
  background-color: #edb6e7b3;
  border-radius: 3%;
}

.form-signin .form-floating:focus-within {
  z-index: 2;
}

.form-signin input[type="text"] {
  margin-bottom: -1px;
  border-radius: 0;
}

.form-signin input[type="password"] {
  border-radius: 0;
  margin-bottom: 10px;
}

.btn-signUp {
  color: #fff;
  background-color: plum;
  --bs-btn-hover-color: #fff;
  --bs-btn-hover-bg: rgb(192, 106, 192);
  --bs-btn-active-color: #fff;
  --bs-btn-active-bg: plum;
}

.btn-signIn {
  color: #fff;
  background-color: palevioletred;
  --bs-btn-hover-color: #fff;
  --bs-btn-hover-bg: rgb(168, 88, 115);
  --bs-btn-active-color: #fff;
  --bs-btn-active-bg: palevioletred;
}
</style>
  